"use client"

import { useState } from 'react'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Button } from '@/components/ui/button'
import { Label } from '@/components/ui/label'
import { Select } from '@/components/ui/select'
import { MultiSelect } from '@/components/ui/multi-select'
import { 
  ImagePlus, 
  X, 
  Plus, 
  Trash2, 
  FileText,
  ClipboardList
} from 'lucide-react'

type IpsClassification = 'IND' | 'BIO' | 'GASES'
type InvimaClassification = 'Clase I' | 'Clase IIA' | 'Clase IIB' | 'Clase III'
type MissionAxis = 'Docencia' | 'Investigación' | 'Extensión'
type RiskClass = 'I' | 'IIA' | 'IIB' | 'III'
type AcquisitionForm = 'Compra directa' | 'Donación' | 'Comodato' | 'Alquiler' | 'Otro'
type MetrologicalFrequency = 'Anual' | 'Semestral' | 'Otro'
type MetrologicalActivity = 'MP' | 'MC' | 'C'

interface MetrologicalHistoryRecord {
  date: string
  reportNumber: string
  activity: MetrologicalActivity[]
  supplier: string
  observations: string
}

interface ObservationRecord {
  date: string
  name: string
  observation: string
}

export default function AddEquipmentPage() {
  const [image, setImage] = useState<File | null>(null)
  const [imagePreview, setImagePreview] = useState<string | null>(null)

  const [formData, setFormData] = useState({
    // Identificación
    name: '',
    udeaCode: '',
    ipsCode: '',
    ecriCode: '',
    brand: '',
    model: '',
    serialNumber: '',
    missionAxisClassification: [] as MissionAxis[],
    ipsClassification: null as IpsClassification | null,
    invimaClassification: null as InvimaClassification | null,
    invimaCode: '',
    manufactureDate: '',
    usefulLife: '',
    sede: '',
    service: '',
    location: '',
    responsible: '',
    riskClass: '' as RiskClass | '',
    components: '',
    
    // Registro histórico
    acquisitionForm: '' as AcquisitionForm | '',
    documentType: '',
    documentNumber: '',
    acquisitionDate: '',
    warrantyReplacementDate: '',
    warrantyExpiration: '',
    supplier: '',
    supplierNIT: '',
    supplierContact: '',
    
    // Condiciones de funcionamiento
    voltage: '',
    current: '',
    relativeHumidity: '',
    temperature: '',
    dimensions: '',
    weight: '',
    otherConditions: '',
    
    // Datos metrológicos
    measurementMagnitude: '',
    equipmentRange: '',
    serviceUsageRange: '',
    resolution: '',
    maxAllowedError: '',
    metrologicalFrequencyOption: '' as MetrologicalFrequency | '',
    metrologicalFrequencyOther: '',
    requiresCalibration: false,
    
    // Registros históricos metrológicos
    metrologicalHistory: [] as MetrologicalHistoryRecord[],
    
    // Observaciones
    observations: [] as ObservationRecord[],
  })

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      setImage(file)
      const reader = new FileReader()
      reader.onloadend = () => {
        setImagePreview(reader.result as string)
      }
      reader.readAsDataURL(file)
    }
  }

  const removeImage = () => {
    setImage(null)
    setImagePreview(null)
  }

  const addHistoryRecord = () => {
    setFormData(prev => ({
      ...prev,
      metrologicalHistory: [
        ...prev.metrologicalHistory,
        { date: '', reportNumber: '', activity: [], supplier: '', observations: '' }
      ]
    }))
  }

  const removeHistoryRecord = (index: number) => {
    setFormData(prev => ({
      ...prev,
      metrologicalHistory: prev.metrologicalHistory.filter((_, i) => i !== index)
    }))
  }

  const toggleHistoryActivity = (index: number, activity: MetrologicalActivity) => {
    setFormData(prev => {
      const newHistory = [...prev.metrologicalHistory]
      const record = newHistory[index]
      if (record) {
        const activityIndex = record.activity.indexOf(activity)
        if (activityIndex > -1) {
          record.activity.splice(activityIndex, 1)
        } else {
          record.activity.push(activity)
        }
        newHistory[index] = record
      }
      return { ...prev, metrologicalHistory: newHistory }
    })
  }

  const updateHistoryRecord = (index: number, field: keyof MetrologicalHistoryRecord, value: string) => {
    setFormData(prev => {
      const newHistory = [...prev.metrologicalHistory]
      if (newHistory[index]) {
        newHistory[index] = { ...newHistory[index], [field]: value }
      }
      return { ...prev, metrologicalHistory: newHistory }
    })
  }

  const addObservation = () => {
    setFormData(prev => ({
      ...prev,
      observations: [...prev.observations, { date: '', name: '', observation: '' }]
    }))
  }

  const removeObservation = (index: number) => {
    setFormData(prev => ({
      ...prev,
      observations: prev.observations.filter((_, i) => i !== index)
    }))
  }

  const updateObservation = (index: number, field: keyof ObservationRecord, value: string) => {
    setFormData(prev => {
      const newObservations = [...prev.observations]
      if (newObservations[index]) {
        newObservations[index] = { ...newObservations[index], [field]: value }
      }
      return { ...prev, observations: newObservations }
    })
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log('Form data:', { ...formData, image })
  }

  return (
    <div className="w-full space-y-8 pb-8">
      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Información general */}
        <div className="rounded-2xl border border-slate-200 bg-white shadow-sm overflow-hidden">
          <div className="border-b border-slate-200 bg-gradient-to-r from-emerald-50 to-teal-50 px-8 py-6 text-center">
            <h2 className="text-xl font-bold text-slate-900">Información general</h2>
            <p className="text-sm text-slate-600 mt-1">Datos básicos de identificación del equipo</p>
          </div>
          
          <div className="p-8 space-y-6">
            {/* Imagen y datos básicos */}
            <div className="grid gap-8 lg:grid-cols-[1fr_2fr]">
              {/* Imagen */}
              <div className="space-y-3">
                <Label className="text-sm font-semibold text-slate-700">Imagen del equipo</Label>
                <label htmlFor="image-upload" className="cursor-pointer group block">
                  {imagePreview ? (
                    <div className="relative w-full">
                      <div className="aspect-[4/3] w-full">
                        <img
                          src={imagePreview}
                          alt="Vista previa"
                          className="h-full w-full rounded-2xl object-cover border-2 border-slate-200 transition-all group-hover:border-emerald-400 shadow-lg"
                        />
                      </div>
                      <button
                        type="button"
                        onClick={(e) => {
                          e.preventDefault()
                          removeImage()
                        }}
                        className="absolute -top-2 -right-2 h-8 w-8 rounded-full bg-red-500 text-white flex items-center justify-center hover:bg-red-600 transition shadow-lg"
                      >
                        <X className="h-4 w-4" />
                      </button>
                    </div>
                  ) : (
                    <div className="aspect-[4/3] w-full rounded-2xl border-2 border-dashed border-slate-300 flex flex-col items-center justify-center bg-slate-50 transition-all group-hover:border-emerald-400 group-hover:bg-emerald-50/50">
                      <ImagePlus className="h-12 w-12 text-slate-400 mb-2 transition-colors group-hover:text-emerald-600" />
                      <span className="text-sm font-semibold text-slate-500 group-hover:text-emerald-700">Subir imagen</span>
                      <span className="text-xs text-slate-400 mt-1">JPG, PNG, WebP</span>
                      <span className="text-xs text-slate-400">(Máx. 5MB)</span>
                    </div>
                  )}
                  <input
                    id="image-upload"
                    type="file"
                    accept="image/*"
                    onChange={handleImageChange}
                    className="hidden"
                  />
                </label>
              </div>

              {/* Datos básicos */}
              <div className="grid gap-4 md:grid-cols-2">
                <div className="space-y-2 md:col-span-2">
                  <Label className="text-sm">Nombre del equipo</Label>
                  <Input
                    value={formData.name}
                    onChange={(e) => setFormData(prev => ({ ...prev, name: e.target.value }))}
                    placeholder="Ej: LUMINEX - fluoroanalizador"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-sm">Código UdeA</Label>
                  <Input
                    value={formData.udeaCode}
                    onChange={(e) => setFormData(prev => ({ ...prev, udeaCode: e.target.value }))}
                    placeholder="BIO-16869"
                    className="font-mono"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-sm">Código IPS</Label>
                  <Input
                    value={formData.ipsCode}
                    onChange={(e) => setFormData(prev => ({ ...prev, ipsCode: e.target.value }))}
                    placeholder="180235"
                    className="font-mono"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-sm">Código ECRI</Label>
                  <Input
                    value={formData.ecriCode}
                    onChange={(e) => setFormData(prev => ({ ...prev, ecriCode: e.target.value }))}
                    placeholder="008811"
                    className="font-mono"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-sm">Marca</Label>
                  <Input
                    value={formData.brand}
                    onChange={(e) => setFormData(prev => ({ ...prev, brand: e.target.value }))}
                    placeholder="Ej: LUMINEX"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-sm">Modelo</Label>
                  <Input
                    value={formData.model}
                    onChange={(e) => setFormData(prev => ({ ...prev, model: e.target.value }))}
                    placeholder="Ej: LUMINEX LX 100 / 200"
                  />
                </div>
                <div className="space-y-2">
                  <Label className="text-sm">Serie</Label>
                  <Input
                    value={formData.serialNumber}
                    onChange={(e) => setFormData(prev => ({ ...prev, serialNumber: e.target.value }))}
                    placeholder="Ej: LX10017065423"
                    className="font-mono"
                  />
                </div>
              </div>
            </div>

            {/* Clasificaciones */}
            <div className="grid gap-4 md:grid-cols-3">
              <div className="space-y-2">
                <Label className="text-sm">Clasificación por eje misional</Label>
                <MultiSelect
                  options={['Docencia', 'Investigación', 'Extensión']}
                  value={formData.missionAxisClassification}
                  onChange={(value) => setFormData(prev => ({ ...prev, missionAxisClassification: value as MissionAxis[] }))}
                  placeholder="Selecciona uno o más ejes"
                />
              </div>

              <div className="space-y-2">
                <Label className="text-sm">Clasificación IPS</Label>
                <Select
                  value={formData.ipsClassification || ''}
                  onChange={(e) => setFormData(prev => ({ ...prev, ipsClassification: e.target.value as IpsClassification || null }))}
                >
                  <option value="">Selecciona una opción</option>
                  <option value="IND">IND</option>
                  <option value="BIO">BIO</option>
                  <option value="GASES">GASES</option>
                </Select>
              </div>

              <div className="space-y-2">
                <Label className="text-sm">Clasificación Invima</Label>
                <Select
                  value={formData.invimaClassification || ''}
                  onChange={(e) => setFormData(prev => ({ ...prev, invimaClassification: e.target.value as InvimaClassification || null }))}
                >
                  <option value="">Selecciona una clase</option>
                  <option value="Clase I">Clase I</option>
                  <option value="Clase IIA">Clase IIA</option>
                  <option value="Clase IIB">Clase IIB</option>
                  <option value="Clase III">Clase III</option>
                </Select>
              </div>
            </div>

            {/* Código registro Invima (solo si tiene clasificación) */}
            {formData.invimaClassification && (
              <div className="space-y-2">
                <Label className="text-sm">Código del registro Invima</Label>
                <Input
                  value={formData.invimaCode}
                  onChange={(e) => setFormData(prev => ({ ...prev, invimaCode: e.target.value }))}
                  placeholder="INVIMA 2020DM-0022003"
                  className="font-mono"
                />
              </div>
            )}

            {/* Fechas */}
            <div className="grid gap-4 md:grid-cols-2">
              <div className="space-y-2">
                <Label className="text-sm">Fecha de fabricación</Label>
                <Input
                  type="date"
                  value={formData.manufactureDate}
                  onChange={(e) => setFormData(prev => ({ ...prev, manufactureDate: e.target.value }))}
                />
              </div>
              <div className="space-y-2">
                <Label className="text-sm">Vida útil</Label>
                <Input
                  value={formData.usefulLife}
                  onChange={(e) => setFormData(prev => ({ ...prev, usefulLife: e.target.value }))}
                  placeholder="Ej: 10 años"
                />
              </div>
            </div>

            {/* Ubicación y responsable */}
            <div className="grid gap-4 md:grid-cols-4">
              <div className="space-y-2">
                <Label className="text-sm">Sede</Label>
                <Input
                  value={formData.sede}
                  onChange={(e) => setFormData(prev => ({ ...prev, sede: e.target.value }))}
                  placeholder="Ej: SIU"
                />
              </div>
              <div className="space-y-2">
                <Label className="text-sm">Servicio</Label>
                <Input
                  value={formData.service}
                  onChange={(e) => setFormData(prev => ({ ...prev, service: e.target.value }))}
                  placeholder="Ej: Laboratorio trasplantes"
                />
              </div>
              <div className="space-y-2">
                <Label className="text-sm">Ubicación</Label>
                <Input
                  value={formData.location}
                  onChange={(e) => setFormData(prev => ({ ...prev, location: e.target.value }))}
                  placeholder="Ej: Torre 1, Lab 410"
                />
              </div>
              <div className="space-y-2">
                <Label className="text-sm">Responsable</Label>
                <Input
                  value={formData.responsible}
                  onChange={(e) => setFormData(prev => ({ ...prev, responsible: e.target.value }))}
                  placeholder="Ej: Juan Jose Serrano"
                />
              </div>
            </div>

            {/* Clasificación por riesgo */}
            <div className="space-y-3">
              <Label>Clasificación por riesgo</Label>
              <div className="grid grid-cols-5 gap-2">
                {(['I', 'IIA', 'IIB', 'III'] as RiskClass[]).map((risk) => (
                  <button
                    key={risk}
                    type="button"
                    onClick={() => setFormData(prev => ({ ...prev, riskClass: prev.riskClass === risk ? '' : risk }))}
                    className={`px-3 py-3 rounded-xl border-2 text-sm font-semibold transition ${
                      formData.riskClass === risk
                        ? 'border-emerald-500 bg-emerald-50 text-emerald-700'
                        : 'border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50'
                    }`}
                  >
                    {risk}
                  </button>
                ))}
                <button
                  type="button"
                  className="px-3 py-3 rounded-xl border-2 text-sm font-semibold transition border-slate-200 text-slate-600 hover:border-emerald-200 hover:bg-emerald-50/50"
                >
                  N/A
                </button>
              </div>
            </div>

            {/* Componentes */}
            <div className="space-y-2">
              <Label className="text-sm">Componentes / accesorios</Label>
              <Textarea
                value={formData.components}
                onChange={(e) => setFormData(prev => ({ ...prev, components: e.target.value }))}
                placeholder="Lista de componentes, accesorios incluidos..."
                rows={3}
              />
            </div>
          </div>
        </div>

        {/* Adquisición */}
        <div className="rounded-2xl border border-slate-200 bg-white shadow-sm overflow-hidden">
          <div className="border-b border-slate-200 bg-gradient-to-r from-emerald-50 to-teal-50 px-8 py-6 text-center">
            <h2 className="text-xl font-bold text-slate-900">Información de adquisición</h2>
            <p className="text-sm text-slate-600 mt-1">Datos de compra, garantía y proveedor</p>
          </div>
          
          <div className="p-8 space-y-8">
            {/* Forma de adquisición */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Forma de adquisición
              </h3>
              <div className="grid grid-cols-2 md:grid-cols-5 gap-3">
                {(['Compra directa', 'Donación', 'Comodato', 'Alquiler', 'Otro'] as AcquisitionForm[]).map((option) => (
                  <button
                    key={option}
                    type="button"
                    onClick={() => setFormData(prev => ({ ...prev, acquisitionForm: prev.acquisitionForm === option ? '' : option }))}
                    className={`px-4 py-3 rounded-xl border-2 text-sm font-semibold transition ${
                      formData.acquisitionForm === option
                        ? 'border-purple-500 bg-purple-50 text-purple-700'
                        : 'border-slate-200 text-slate-600 hover:border-purple-200 hover:bg-purple-50/50'
                    }`}
                  >
                    {option}
                  </button>
                ))}
              </div>
            </div>

            {/* Documentación */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Documentación
              </h3>
              <div className="grid gap-5 md:grid-cols-2">
                <div className="space-y-2">
                  <Label>Tipo de documento</Label>
                  <Input
                    value={formData.documentType}
                    onChange={(e) => setFormData(prev => ({ ...prev, documentType: e.target.value }))}
                    placeholder="Ej: Factura, Orden de compra"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Número de documento</Label>
                  <Input
                    value={formData.documentNumber}
                    onChange={(e) => setFormData(prev => ({ ...prev, documentNumber: e.target.value }))}
                    placeholder="Número"
                    className="font-mono"
                  />
                </div>
              </div>
            </div>

            {/* Fechas importantes */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Fechas importantes
              </h3>
              <div className="grid gap-5 md:grid-cols-3">
                <div className="space-y-2">
                  <Label>Fecha de adquisición</Label>
                  <Input
                    type="date"
                    value={formData.acquisitionDate}
                    onChange={(e) => setFormData(prev => ({ ...prev, acquisitionDate: e.target.value }))}
                  />
                </div>
                <div className="space-y-2">
                  <Label>Cambio por garantía</Label>
                  <Input
                    type="date"
                    value={formData.warrantyReplacementDate}
                    onChange={(e) => setFormData(prev => ({ ...prev, warrantyReplacementDate: e.target.value }))}
                  />
                </div>
                <div className="space-y-2">
                  <Label>Vencimiento garantía</Label>
                  <Input
                    type="date"
                    value={formData.warrantyExpiration}
                    onChange={(e) => setFormData(prev => ({ ...prev, warrantyExpiration: e.target.value }))}
                  />
                </div>
              </div>
            </div>

            {/* Proveedor */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Información del proveedor
              </h3>
              <div className="grid gap-5 md:grid-cols-2">
                <div className="space-y-2">
                  <Label>Nombre del proveedor</Label>
                  <Input
                    value={formData.supplier}
                    onChange={(e) => setFormData(prev => ({ ...prev, supplier: e.target.value }))}
                    placeholder="Nombre o razón social"
                  />
                </div>
                <div className="space-y-2">
                  <Label>NIT</Label>
                  <Input
                    value={formData.supplierNIT}
                    onChange={(e) => setFormData(prev => ({ ...prev, supplierNIT: e.target.value }))}
                    placeholder="NIT del proveedor"
                    className="font-mono"
                  />
                </div>
              </div>
              <div className="space-y-2">
                <Label>Información de contacto</Label>
                <Textarea
                  value={formData.supplierContact}
                  onChange={(e) => setFormData(prev => ({ ...prev, supplierContact: e.target.value }))}
                  placeholder="Nombre del contacto, teléfono, correo electrónico..."
                  rows={3}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Especificaciones técnicas */}
        <div className="rounded-2xl border border-slate-200 bg-white shadow-sm overflow-hidden">
          <div className="border-b border-slate-200 bg-gradient-to-r from-emerald-50 to-teal-50 px-8 py-6 text-center">
            <h2 className="text-xl font-bold text-slate-900">Especificaciones técnicas</h2>
            <p className="text-sm text-slate-600 mt-1">Condiciones de funcionamiento y requisitos</p>
          </div>
          
          <div className="p-8 space-y-8">
            {/* Condiciones eléctricas */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Condiciones eléctricas
              </h3>
              <div className="grid gap-5 md:grid-cols-2">
                <div className="space-y-2">
                  <Label>Voltaje</Label>
                  <Input
                    value={formData.voltage}
                    onChange={(e) => setFormData(prev => ({ ...prev, voltage: e.target.value }))}
                    placeholder="Ej: 110-220V"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Corriente</Label>
                  <Input
                    value={formData.current}
                    onChange={(e) => setFormData(prev => ({ ...prev, current: e.target.value }))}
                    placeholder="Ej: 5A"
                  />
                </div>
              </div>
            </div>

            {/* Condiciones ambientales */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Condiciones ambientales
              </h3>
              <div className="grid gap-5 md:grid-cols-2">
                <div className="space-y-2">
                  <Label>Humedad relativa</Label>
                  <Input
                    value={formData.relativeHumidity}
                    onChange={(e) => setFormData(prev => ({ ...prev, relativeHumidity: e.target.value }))}
                    placeholder="Ej: 30-75%"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Temperatura</Label>
                  <Input
                    value={formData.temperature}
                    onChange={(e) => setFormData(prev => ({ ...prev, temperature: e.target.value }))}
                    placeholder="Ej: 15-30°C"
                  />
                </div>
              </div>
            </div>

            {/* Dimensiones y peso */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Dimensiones y peso
              </h3>
              <div className="grid gap-5 md:grid-cols-2">
                <div className="space-y-2">
                  <Label>Dimensiones</Label>
                  <Input
                    value={formData.dimensions}
                    onChange={(e) => setFormData(prev => ({ ...prev, dimensions: e.target.value }))}
                    placeholder="Ej: 50x40x30 cm (L x A x P)"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Peso</Label>
                  <Input
                    value={formData.weight}
                    onChange={(e) => setFormData(prev => ({ ...prev, weight: e.target.value }))}
                    placeholder="Ej: 15 kg"
                  />
                </div>
              </div>
            </div>

            {/* Otros requisitos */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Requisitos adicionales
              </h3>
              <div className="space-y-2">
                <Textarea
                  value={formData.otherConditions}
                  onChange={(e) => setFormData(prev => ({ ...prev, otherConditions: e.target.value }))}
                  placeholder="Otros requisitos especiales de funcionamiento u operación..."
                  rows={3}
                />
              </div>
            </div>
          </div>
        </div>

        {/* Información metrológica */}
        <div className="rounded-2xl border border-slate-200 bg-white shadow-sm overflow-hidden">
          <div className="border-b border-slate-200 bg-gradient-to-r from-emerald-50 to-teal-50 px-8 py-6 text-center">
            <h2 className="text-xl font-bold text-slate-900">Información metrológica</h2>
            <p className="text-sm text-slate-600 mt-1">Datos de calibración y especificaciones de medida</p>
          </div>
          
          <div className="p-8 space-y-8">
            {/* Parámetros de medición */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Parámetros de medición
              </h3>
              <div className="grid gap-5 md:grid-cols-3">
                <div className="space-y-2">
                  <Label>Magnitud de medida</Label>
                  <Input
                    value={formData.measurementMagnitude}
                    onChange={(e) => setFormData(prev => ({ ...prev, measurementMagnitude: e.target.value }))}
                    placeholder="Ej: Presión, Temperatura"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Rango del equipo</Label>
                  <Input
                    value={formData.equipmentRange}
                    onChange={(e) => setFormData(prev => ({ ...prev, equipmentRange: e.target.value }))}
                    placeholder="Ej: 0-200 mmHg"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Rango de uso en servicio</Label>
                  <Input
                    value={formData.serviceUsageRange}
                    onChange={(e) => setFormData(prev => ({ ...prev, serviceUsageRange: e.target.value }))}
                    placeholder="Rango habitual"
                  />
                </div>
              </div>
            </div>

            {/* Precisión */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Precisión y tolerancias
              </h3>
              <div className="grid gap-5 md:grid-cols-2">
                <div className="space-y-2">
                  <Label>Resolución</Label>
                  <Input
                    value={formData.resolution}
                    onChange={(e) => setFormData(prev => ({ ...prev, resolution: e.target.value }))}
                    placeholder="Ej: 0.1 mmHg"
                  />
                </div>
                <div className="space-y-2">
                  <Label>Error máximo permitido</Label>
                  <Input
                    value={formData.maxAllowedError}
                    onChange={(e) => setFormData(prev => ({ ...prev, maxAllowedError: e.target.value }))}
                    placeholder="Ej: ±2%"
                  />
                </div>
              </div>
            </div>

            {/* Calibración */}
            <div className="space-y-5">
              <h3 className="text-xs font-bold uppercase tracking-wider text-slate-500 pb-2 border-b border-slate-200">
                Configuración de calibración
              </h3>
              <div className="grid gap-6 md:grid-cols-2">
                <div className="space-y-3">
                  <Label>Frecuencia de calibración</Label>
                  <div className="flex gap-2">
                    {(['Anual', 'Semestral', 'Otro'] as MetrologicalFrequency[]).map((option) => (
                      <button
                        key={option}
                        type="button"
                        onClick={() => setFormData(prev => ({ 
                          ...prev, 
                          metrologicalFrequencyOption: prev.metrologicalFrequencyOption === option ? '' : option,
                          metrologicalFrequencyOther: prev.metrologicalFrequencyOption === option ? '' : prev.metrologicalFrequencyOther
                        }))}
                        className={`flex-1 px-4 py-3 rounded-xl border-2 text-sm font-semibold transition ${
                          formData.metrologicalFrequencyOption === option
                            ? 'border-cyan-500 bg-cyan-50 text-cyan-700'
                            : 'border-slate-200 text-slate-600 hover:border-cyan-200 hover:bg-cyan-50/50'
                        }`}
                      >
                        {option}
                      </button>
                    ))}
                  </div>
                  {formData.metrologicalFrequencyOption === 'Otro' && (
                    <Input
                      value={formData.metrologicalFrequencyOther}
                      onChange={(e) => setFormData(prev => ({ ...prev, metrologicalFrequencyOther: e.target.value }))}
                      placeholder="Especifica la frecuencia"
                    />
                  )}
                </div>

                <div className="space-y-3">
                  <Label>¿Requiere calibración o calificación?</Label>
                  <div className="flex gap-2">
                    <button
                      type="button"
                      onClick={() => setFormData(prev => ({ ...prev, requiresCalibration: true }))}
                      className={`flex-1 px-4 py-3 rounded-xl border-2 text-sm font-semibold transition ${
                        formData.requiresCalibration
                          ? 'border-cyan-500 bg-cyan-50 text-cyan-700'
                          : 'border-slate-200 text-slate-600 hover:border-cyan-200 hover:bg-cyan-50/50'
                      }`}
                    >
                      Sí, requiere
                    </button>
                    <button
                      type="button"
                      onClick={() => setFormData(prev => ({ ...prev, requiresCalibration: false }))}
                      className={`flex-1 px-4 py-3 rounded-xl border-2 text-sm font-semibold transition ${
                        !formData.requiresCalibration
                          ? 'border-cyan-500 bg-cyan-50 text-cyan-700'
                          : 'border-slate-200 text-slate-600 hover:border-cyan-200 hover:bg-cyan-50/50'
                      }`}
                    >
                      No requiere
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Historial metrológico */}
        <div className="rounded-2xl border border-slate-200 bg-white shadow-sm overflow-hidden">
          <div className="border-b border-slate-200 bg-gradient-to-r from-emerald-50 to-teal-50 px-8 py-6">
            <div className="flex items-center justify-between">
              <div className="flex-1 text-center">
                <h2 className="text-xl font-bold text-slate-900">Historial metrológico</h2>
                <p className="text-sm text-slate-600 mt-1">Registros de calibraciones y mantenimientos previos</p>
              </div>
              <Button
                type="button"
                variant="outline"
                size="sm"
                onClick={addHistoryRecord}
              >
                <Plus className="h-4 w-4" />
                Agregar registro
              </Button>
            </div>
          </div>
          
          <div className="p-8">
            {formData.metrologicalHistory.length === 0 ? (
              <div className="rounded-xl bg-slate-50 border-2 border-dashed border-slate-300 p-12 text-center">
                <FileText className="h-12 w-12 text-slate-400 mx-auto mb-3" />
                <p className="text-base font-medium text-slate-600 mb-1">
                  No hay registros metrológicos
                </p>
                <p className="text-sm text-slate-500">
                  Haz clic en "Agregar registro" para comenzar
                </p>
              </div>
            ) : (
              <div className="space-y-4">
                {formData.metrologicalHistory.map((record, index) => (
                  <div key={index} className="rounded-xl border-2 border-slate-200 bg-slate-50/50 p-6 space-y-5 hover:border-indigo-200 transition">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-bold uppercase tracking-wider text-slate-500">
                        Registro #{index + 1}
                      </span>
                      <Button
                        type="button"
                        variant="ghost"
                        size="sm"
                        onClick={() => removeHistoryRecord(index)}
                        className="text-red-600 hover:text-red-700 hover:bg-red-50"
                      >
                        <Trash2 className="h-4 w-4" />
                        Eliminar
                      </Button>
                    </div>
                    
                    <div className="grid gap-4 md:grid-cols-3">
                      <div className="space-y-2">
                        <Label className="text-xs">Fecha</Label>
                        <Input
                          value={record.date}
                          onChange={(e) => updateHistoryRecord(index, 'date', e.target.value)}
                          placeholder="DD/MM/AAAA"
                          className="bg-white"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label className="text-xs">Número de informe</Label>
                        <Input
                          value={record.reportNumber}
                          onChange={(e) => updateHistoryRecord(index, 'reportNumber', e.target.value)}
                          placeholder="N° de informe"
                          className="bg-white font-mono"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label className="text-xs">Proveedor del servicio</Label>
                        <Input
                          value={record.supplier}
                          onChange={(e) => updateHistoryRecord(index, 'supplier', e.target.value)}
                          placeholder="Nombre del proveedor"
                          className="bg-white"
                        />
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label className="text-xs">Tipo de actividad</Label>
                      <div className="grid grid-cols-3 gap-2">
                        {(['MP', 'MC', 'C'] as MetrologicalActivity[]).map((activity) => (
                          <button
                            key={activity}
                            type="button"
                            onClick={() => toggleHistoryActivity(index, activity)}
                            className={`px-4 py-2.5 rounded-lg border-2 text-sm font-semibold transition ${
                              record.activity.includes(activity)
                                ? 'border-indigo-500 bg-indigo-50 text-indigo-700'
                                : 'border-slate-200 bg-white text-slate-600 hover:border-indigo-200'
                            }`}
                          >
                            {activity === 'MP' && 'Mantenimiento Preventivo'}
                            {activity === 'MC' && 'Mantenimiento Correctivo'}
                            {activity === 'C' && 'Calibración'}
                          </button>
                        ))}
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label className="text-xs">Observaciones</Label>
                      <Textarea
                        value={record.observations}
                        onChange={(e) => updateHistoryRecord(index, 'observations', e.target.value)}
                        placeholder="Detalles y observaciones del registro..."
                        rows={2}
                        className="bg-white"
                      />
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Observaciones adicionales */}
        <div className="rounded-2xl border border-slate-200 bg-white shadow-sm overflow-hidden">
          <div className="border-b border-slate-200 bg-gradient-to-r from-emerald-50 to-teal-50 px-8 py-6">
            <div className="flex items-center justify-between">
              <div className="flex-1 text-center">
                <h2 className="text-xl font-bold text-slate-900">Observaciones adicionales</h2>
                <p className="text-sm text-slate-600 mt-1">Notas y comentarios importantes sobre el equipo</p>
              </div>
              <Button
                type="button"
                variant="outline"
                size="sm"
                onClick={addObservation}
              >
                <Plus className="h-4 w-4" />
                Agregar observación
              </Button>
            </div>
          </div>
          
          <div className="p-8">
            {formData.observations.length === 0 ? (
              <div className="rounded-xl bg-slate-50 border-2 border-dashed border-slate-300 p-12 text-center">
                <ClipboardList className="h-12 w-12 text-slate-400 mx-auto mb-3" />
                <p className="text-base font-medium text-slate-600 mb-1">
                  No hay observaciones registradas
                </p>
                <p className="text-sm text-slate-500">
                  Haz clic en "Agregar observación" para comenzar
        </p>
      </div>
            ) : (
              <div className="space-y-4">
                {formData.observations.map((observation, index) => (
                  <div key={index} className="rounded-xl border-2 border-slate-200 bg-slate-50/50 p-6 space-y-5 hover:border-rose-200 transition">
                    <div className="flex items-center justify-between">
                      <span className="text-xs font-bold uppercase tracking-wider text-slate-500">
                        Observación #{index + 1}
                      </span>
                      <Button
                        type="button"
                        variant="ghost"
                        size="sm"
                        onClick={() => removeObservation(index)}
                        className="text-red-600 hover:text-red-700 hover:bg-red-50"
                      >
                        <Trash2 className="h-4 w-4" />
                        Eliminar
                      </Button>
                    </div>
                    
                    <div className="grid gap-4 md:grid-cols-2">
                      <div className="space-y-2">
                        <Label className="text-xs">Fecha</Label>
                        <Input
                          value={observation.date}
                          onChange={(e) => updateObservation(index, 'date', e.target.value)}
                          placeholder="DD/MM/AAAA"
                          className="bg-white"
                        />
                      </div>
                      <div className="space-y-2">
                        <Label className="text-xs">Responsable</Label>
                        <Input
                          value={observation.name}
                          onChange={(e) => updateObservation(index, 'name', e.target.value)}
                          placeholder="Nombre del responsable"
                          className="bg-white"
                        />
                      </div>
                    </div>

                    <div className="space-y-2">
                      <Label className="text-xs">Observación</Label>
                      <Textarea
                        value={observation.observation}
                        onChange={(e) => updateObservation(index, 'observation', e.target.value)}
                        placeholder="Detalle de la observación..."
                        rows={3}
                        className="bg-white"
                      />
                    </div>
                  </div>
                ))}
              </div>
            )}
          </div>
        </div>

        {/* Botones de acción */}
        <div className="flex items-center justify-end gap-4 pt-4">
          <Button type="button" variant="outline" size="lg" className="min-w-[140px]">
            Cancelar
          </Button>
          <Button type="submit" size="lg" className="min-w-[180px]">
            Guardar equipo
          </Button>
        </div>
      </form>
    </div>
  )
}
